import React, { useState } from 'react';

const SliderImage = props => {

    const [loading, setLoading] = useState(false);

    const onLoadingHandler = () => {
        setLoading(true);
    }

    const img = require(`../assets/image/${props.image}`);

    return (
        <React.Fragment>
            <div className="">
                <img
                    className={loading ? "slider-image" : "hide"}
                    src={img.default}
                    alt="test"
                    onLoad={onLoadingHandler} />

            </div>
            <div className={loading ? "hide" : "loader"}>
                <div className="loading_text">
                    Loading...
                </div>
            </div>
        </React.Fragment>
    )
}

export default SliderImage;