import React,{useState} from 'react';
import {Card} from 'react-bootstrap';

const Cards = props => {
    const [loading,setLoading] = useState(false); 

    const onImageLoadHandler = () => {
      setLoading(true);
    }

    const image = require(`../assets/image/${props.cardImage}`);

    return(
        <div className="card_margin" onClick={props.click}>
         <Card>
            <Card.Img 
              variant="top" 
              src={image.default} 
              onLoad={onImageLoadHandler} 
              className={loading ? "" : "hide"} />
            <div className={loading ? "hide" : "card_loader" }>
              <div className="card_loader_text">Loading...</div>  
            </div>
            
            <Card.Body className="card-body">
              <Card.Title className="abs-center inner">{props.title}</Card.Title>
              <Card.Text>
                {props.text}
              </Card.Text>
            </Card.Body>
          </Card>
          
        </div>
      );
}

export default Cards;