import React, { useContext } from 'react';
import { Carousel } from 'react-bootstrap';
import AuthContext from '../context/auth-context';
import { ButtonGroup, Button } from 'react-bootstrap';

import SliderImages from './SliderImages';

const BlogDetails = props => {

    const authContext = useContext(AuthContext);

    const exactImgs = authContext.filter(blog => blog.id === props.match.params.id);

    const onClickHandler = () => {
        props.history.push('/');
    }

    const images = exactImgs[0].image_collection.map((img, index) =>
        <Carousel.Item interval={2500} key={img + index}>
            <SliderImages image={img} />
        </Carousel.Item>
    );
    return (
        <div className="blog-container">
            <Carousel>
                {images}
            </Carousel>
            <div className="back-button-container" >
                <div className="back-button" onClick={onClickHandler}>Back</div> 
            </div>
        </div>
    );
}

export default BlogDetails;