import React, { useContext } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Card from '../components/card';
import AuthContext from '../context/auth-context';

const Home = props => {
  const authContext = useContext(AuthContext);

  const onClickHandler = id => {
    props.history.push('/blog/' + id);
  }

  const cards = () => authContext.map(item =>
    <Col md={3} key={item.id} className="pl-1 pt-1 pr-1 pb-1">
      <Card click={() => onClickHandler(item.id)} title={item.name} text={item.text} cardImage={item.image} />
    </Col>
  );

  return (
    <React.Fragment>
      <Container>
        <Row className="homepage_layout">
          {cards()}
        </Row>
      </Container>
    </React.Fragment>
  );
}

export default Home;